import { sort } from './sort';
export function getComputedAzureReportItems({ report, idKey, labelKey = idKey, sortKey = 'cost', sortDirection = 0 /* asc */, }) {
    return sort(getUnsortedComputedAzureReportItems({
        report,
        idKey,
        labelKey,
        sortDirection,
        sortKey,
    }), {
        key: sortKey,
        direction: sortDirection,
    });
}
export function getUnsortedComputedAzureReportItems({ report, idKey, labelKey = idKey, }) {
    if (!report) {
        return [];
    }
    const itemMap = new Map();
    const visitDataPoint = (dataPoint) => {
        if (dataPoint.values) {
            dataPoint.values.forEach(value => {
                const cost = value.usage ? value.usage.value : value.cost.value;
                const derivedCost = value.derived_cost ? value.derived_cost.value : 0;
                const infrastructureCost = value.infrastructure_cost
                    ? value.infrastructure_cost.value
                    : 0;
                const id = value[idKey];
                let label;
                if (value[labelKey] instanceof Object) {
                    label = value[labelKey].value;
                }
                else {
                    label = value[labelKey];
                }
                if (!itemMap.get(id)) {
                    itemMap.set(id, {
                        cost,
                        deltaPercent: value.delta_percent,
                        deltaValue: value.delta_value,
                        derivedCost,
                        id,
                        infrastructureCost,
                        label,
                        units: value.usage
                            ? value.usage.units
                            : value.cost
                                ? value.cost.units
                                : 'USD',
                    });
                    return;
                }
                itemMap.set(id, Object.assign({}, itemMap.get(id), { cost: itemMap.get(id).cost + cost, derivedCost: itemMap.get(id).derivedCost + derivedCost, infrastructureCost: itemMap.get(id).infrastructureCost + infrastructureCost }));
            });
        }
        for (const key in dataPoint) {
            if (dataPoint[key] instanceof Array) {
                return dataPoint[key].forEach(visitDataPoint);
            }
        }
    };
    if (report && report.data) {
        report.data.forEach(visitDataPoint);
    }
    return Array.from(itemMap.values());
}
export function getIdKeyForGroupBy(groupBy = {}) {
    if (groupBy.subscription_guid) {
        return 'subscription_guid';
    }
    if (groupBy.instance_type) {
        return 'instance_type';
    }
    if (groupBy.resource_location) {
        return 'resource_location';
    }
    if (groupBy.service_name) {
        return 'service_name';
    }
    return 'date';
}
