import { getQuery } from 'api/ocpQuery';
import React from 'react';
import { translate } from 'react-i18next';
import { connect } from 'react-redux';
import { createMapStateToProps } from 'store/common';
import { ocpReportsActions, ocpReportsSelectors } from 'store/ocpReports';
const reportType = "tag" /* tag */;
class DetailsTagViewBase extends React.Component {
    constructor() {
        super(...arguments);
        this.getTags = () => {
            const { report } = this.props;
            const tags = [];
            if (report) {
                for (const tag of report.data) {
                    for (const val of tag.values) {
                        tags.push(`${tag.key}: ${val}`);
                    }
                }
            }
            return tags;
        };
    }
    componentDidMount() {
        const { fetchReport, queryString } = this.props;
        fetchReport(reportType, queryString);
    }
    componentDidUpdate(prevProps) {
        const { fetchReport, queryString } = this.props;
        if (prevProps.queryString !== queryString) {
            fetchReport(reportType, queryString);
        }
    }
    render() {
        const tags = this.getTags();
        return tags.map((tag, index) => React.createElement("div", { key: `tag-${index}` }, tag));
    }
}
const mapStateToProps = createMapStateToProps((state, { project }) => {
    const queryString = getQuery({
        filter: {
            project,
            resolution: 'monthly',
            time_scope_units: 'month',
            time_scope_value: -1,
        },
    });
    const report = ocpReportsSelectors.selectReport(state, reportType, queryString);
    const reportFetchStatus = ocpReportsSelectors.selectReportFetchStatus(state, reportType, queryString);
    return {
        queryString,
        report,
        reportFetchStatus,
    };
});
const mapDispatchToProps = {
    fetchReport: ocpReportsActions.fetchReport,
};
const DetailsTagView = translate()(connect(mapStateToProps, mapDispatchToProps)(DetailsTagViewBase));
export { DetailsTagView };
